.c-button {
  @apply --typeset-ui;
  display: inline-block;
  padding: map(spaces, small) map(spaces, medium);
  letter-spacing: 1px;
  font-size: .73em;
  font-weight: 500;
  border-radius: 3px;
  text-transform: uppercase;
  background-color: map(colors, primary, base);
  color: #fff;
  text-decoration: none;
}
